<template>
	<view class="comment-son" v-if="show" >
		<view class="" @click="sonPage()">
			<view class="every-comment"  v-for="(item,index) in data"  :key='index'>
				<text class='username'>{{item.username}}:</text>
				<view :style="{textIndent:textIndent}" class="rich-text" v-html="item.txt"></view>
				<view class="example-body">
				</view>
			</view>
		</view>
		 
		<uni-popup id="popup" ref="popup" type="bottom" :animation="true">
		 	<view class="popup-top">{{storey}}楼的回复</view>
			{{reply}}
		 </uni-popup>
	</view>
</template>

<script>
	import com from '../com/com.vue'
	export default{
		name:'commentSon',
		components:{com},
		data(){
			return{
				data:[],
				show:false,
				textIndent:'20px',
			}
		},
		props:['reply','storey','fatherid'],
		created() {
			this.$request('/queryComSon',{
				fatherid:this.fatherid
			},'post').then(res =>{
					this.data = res
					this.show = true
			})
		},
		updated(){
			// #ifdef H5 || APP-PLUS
			const query = uni.createSelectorQuery().in(this);
			query.select('.username').boundingClientRect(data => {
				this.$nextTick(() =>{
					this.textIndent = ((data.width+5)+ 'px' )
				})
			}).exec()
			// #endif
		},
		mounted() {
			//#ifdef MP-WEIXIN 
				const query = uni.createSelectorQuery().in(this);
				query.select('.username').boundingClientRect(data => {
						this.textIndent = ((data.width+5)+ 'px' )
				}).exec()
			//#endif
		},
		methods:{
			sonPage(){
				this.$refs.popup.open()
				console.log(this.reply)
			},
		}
	}
</script>

<style lang="scss">
	.comment-son{
		padding: 5px;
		background-color: #EDEDED;
		.every-comment{
			position: relative;
			.username{
				display: inline-block;
				font-size: 14px;
				height: 20px;
				line-height: 20px;
				font-weight:900 ;
			}
			.rich-text{
				position: relative;
				font-size: 15px;
				word-wrap:break-word;
				line-height: 20px;
				margin-top: -20px;
			}
		}
		#popup{
			.popup-top{
				height: 40px;
				line-height: 40px;
				text-align: center;
				background-color: #fff;
			}
		}
	}
</style>
